div.profile{
    display: flex;
}
div.profileImg{
    width: 20%;
    margin-left: 5%;
    margin-top: 2%;
    border-radius: 10%;
}
div.profileInfo{
    margin-left: 2%;
    margin-top: 2%;
    width: 70%;
}
img.profilePhoto{
    border: gold 5px solid;
    border-radius: 10%;
    width: 100%;
    height: 100%;
}
span.profileName{
    font-size: 50px;
    font-family: "Trade Winds";
    margin-left: 6%;
    color: gold;
}
div.profileLookingForAJobDescription{
    font-size: 23px;
    font-family: "Trade Winds";
    color: gold;
}
div.aboutMe{
    font-size: 30px;
    color: gold;
    font-family: "Trade Winds";
}
hr.hr{
    border-radius: 10%;
}
div.titleDiv{
    margin-left: 2%;
    font-family: "Trade Winds";
    font-size: 25px;
    color:gold;
}
div.socialMediaDiv{
    border: 3px gold solid ;
    border-radius: 20px;
    padding: 10px;
}
button.titleBut{
    color: gold;
    background-color: rgb(16, 139, 255);
    border: 3px gold solid ;
    border-radius: 50%;
    width: 3%;
    height: 3%;
    font-size: 25px;
    transition: 0.3s;
}
button.titleBut:hover{
    border-color: rgb(255, 123, 0);
    color: rgb(255, 123, 0);
    background-color: rgb(16, 108, 255);
}